___CSS_LOADER_EXPORT___.push([module.id, ".amount[data-v-1fc33f92]{color:#0b2e13;margin-left:10px}", "",{"version":3,"sources":["webpack://./resources/apps/h5/home/UserBill.vue"],"names":[],"mappings":"AA6DA,yBACA,aAAA,CACA,gBACA","sourcesContent":["<template>\n    <div class=\"container\">\n        <van-tabs :ellipsis=\"false\" :sticky=\"true\" v-model=\"type\" @change=\"handleTabChange\">\n            <van-tab title=\"收入\" name=\"1\"/>\n            <van-tab title=\"支出\" name=\"2\"/>\n        </van-tabs>\n        <van-pull-refresh v-model=\"refreshing\" @refresh=\"onRefresh\">\n            <van-cell\n                    v-for=\"(item,index) in items\"\n                    :key=\"index\"\n                    :title=\"item.detail\"\n                    :label=\"item.created_at\"\n            >\n                <span class=\"amount\">{{item.type===1 ? '+' : '-'}}{{item.amount}}</span>\n            </van-cell>\n        </van-pull-refresh>\n    </div>\n</template>\n\n<script>\n    import ListMixin from \"../lib/ListMixin\";\n    import {bindLoadMore} from \"../lib/loadmore\";\n\n    export default {\n        name: \"UserBill\",\n        mixins: [ListMixin],\n        data() {\n            return {\n                items: [],\n                type: 1\n            }\n        },\n        methods: {\n            fetchList() {\n                this.$get('/user/bill.list', {\n                    type: this.type,\n                    offset: this.offset,\n                    count: this.pageSize\n                }).then(response => {\n                    let {items} = response.result;\n                    if (this.loadMore) {\n                        this.items = this.items.concat(items);\n                    } else {\n                        this.items = items;\n                    }\n                });\n            },\n            handleTabChange() {\n                this.offset = 0;\n                this.loading = true;\n                this.fetchList();\n            }\n        },\n        mounted() {\n            this.fetchList();\n            bindLoadMore(this.onLoadMore);\n        }\n    }\n</script>\n\n<style scoped>\n    .amount {\n        color: #0b2e13;\n        margin-left: 10px;\n    }\n</style>\n"],"sourceRoot":""}]);
